/* About section container */
.about-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  gap: 2vh;
  overflow: hidden;
}

/* Slider container */
.about-container .slider {
  position: relative;
  width: 90svw;
  max-width: 80svh; /* Ensures a square-like scaling */
  height: 50svh;
  border-radius: 1rem;
  overflow: hidden;

}
 .slider-item{

 }
/* Slider inner wrapper for slides */
.about-container .slider-inner {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  transform: translateX(0%);
  transition: transform 0.5s ease-in-out;
}

/* Each slide item */
.about-container .slider-item {
  flex: 0 0 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Images and videos styling */
.about-container img,
.about-container  video {
  max-width: 70svw;
  height: 100%;
  object-fit: cover;
  border-radius: 2vw;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.);

border: 1px solid navy;
}

/* Navigation arrows */
.about-container .slider-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.4);
  color: white;
  border: none;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.about-container .slider-arrow.left {
  left: 1rem;
}

.about-container .slider-arrow.right {
  right: 1rem;
}

/* Description section (now below the slider) */
.about-container .slider-desc {
  margin-top: 1.0vh;
  max-width: 53svw;

  text-align: center;
  font-size: 1.2vw;
  font-family: sans-serif;
}
